.main__button--more-reasons {
  background: rgb(239, 249, 255);
  border: 2px solid rgb(239, 249, 255);
  cursor: pointer;
  border-radius: 30px;
  position: center;
}
.main__button--more-reasons::before {
  content: "More reasons";
  position: center;
  padding: 10px 30px;
  margin-left: 100 px;
  background-color: #f26002;
  font-family: "Roboto", sans-serif;
  font-size: 20px;
  color: #ffff;
  position: absolute;
  border: 2px solid #f26002;
  transform: translate (50%, 50%) scale (0);
  border-radius: 30px;
  transition-duration: 1s;
  transition-timing-function: ease;
  transition-delay: 0s;
  white-space: nowrap;
}

.main__button--more-reasons::after {
  content: "More reasons";
  padding: 10px 30px;
  background-color: #ffff;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 20px;
  position: absolute;
  color: #f26002;
  border: 2px solid #f26002;
  border-radius: 30px;
  transform: translate (-50%, -50%) scale (0);
  transition-duration: 1s;
  transition-timing-function: ease;
  transition-delay: 0s;
  opacity: 0;
}

.main__button--more-reasons:hover::before {
  transform: translate (-50%, -50%) scale (3);
  opacity: 0;
}

.main__button--more-reasons:hover::after {
  transform: translate (-50%, -50%) scale (1);
  opacity: 1;
}
